//---------------------------
// ANCHOR-WRAPPER.ts
//---------------------------

import { Component, Input, Output, EventEmitter } from '@angular/core';
import { IAnchor } from '../../shared-interfaces';


@Component({
  selector: 'n7-anchor-wrapper',
  templateUrl: './anchor-wrapper.html'
})
export class AnchorWrapperComponent {
  @Input() data: IAnchor;
  @Input() classes: string;
  @Output() clicked = new EventEmitter<any>();

  public isExternal(href) {
    return /^http(?:s)?:\/{2}\S+$/.test(href);
  }

  /**
   * Click event handler.
   * Outputs the payload to it's parent component
   */
  onClick(payload) {
    this.clicked.emit(payload);
  }
}
