//---------------------------
// BUBBLECHART.ts
//---------------------------

import { Component, Input, AfterContentChecked } from '@angular/core';
import * as d3 from 'd3'

/**
 * Interface for BubbleChartComponent's "data"
 * 
 * @property containerId (required)
 * @property containerWidth (required)
 * @property containerHeight (required)
 * @property bubblesData (required)
 * @property isForceSimulationEnabled (required)
 * @property classes (optional)
 * @property reset (optional)
*/
export interface IBubbleChartData {
  /**
   * unique identifier for the bubble-chart
   */
  containerId: string;
  /**
   * total width for the bubble-chart
   */
  width: number;
  /**
   * total height for the bubble-chart
   */
  height: number;
  /**
   * data about the bubbles
   */
  data: IBubbleChartDataItem[];
  /**
   * additional css classes
   */
  classes?: string;
  /**
   * list of ID's of selected items
   * (selected items will render with a 'X' icon)
   */
  selected?: string[]
  /**
   * Specify a list of typeOfEntity types (domain),
   * and a list of colors (range), which will be mapped together.
   */
  colorMatch?: {
    domain: string[],
    range: string[],
  }
  /**
   * Define a max and min size for the bubbles
   */
  sizeRange: [number, number]
  /**
   * If defined, transition will be activated
   * and the value will be used as duration
   */
  transition?: number
  /**
   * If set to false, stops the shuffling of the data
   * before rendering.
   */
  shuffle?: boolean
  /**
   * Defines the font-family and the font-weight for each text group
   */
  fontRendering?: {
    label?: {
      family?: string,
      weight?: string,
    },
    counter?: {
      family?: string,
      weight?: string,
    }
  }

  /**
   * expose the draw function outside of n7-frontend/components library
   * this is needed to redraw bubble-chart-component on command
   */
  setDraw?: any;
}

/**
 * Interface for D3Chart's "data"
 *
 * @property entity (required)
 * - id (required)
 * - label (optional)
 * - typeOfEntity (optional)
 * @property count (required)
*/
export interface IBubbleChartDataItem {
  entity: {
    id: string;
    label?: string;
    typeOfEntity?: string;
  }
  count: number;
}

@Component({
  selector: 'n7-bubble-chart',
  templateUrl: './bubble-chart.html'
})
export class BubbleChartComponent implements AfterContentChecked {
  @Input() data: IBubbleChartData;
  @Input() emit: any;
  private _loaded: boolean = false;

  ngAfterContentChecked() {
    /*
     Waits for the dom to be loaded, then fires the draw function
     that renders the chart.
    */
    if (!this.data) {
      return
    } else {
      if (this._loaded) return;
      this._loaded = true;
      setTimeout(() => {
        this.draw();
        if (this.data && this.data.setDraw) {
          this.data.setDraw(this.draw)
        }
      })
    }
  }

  onClick(payload) {
    if (!this.emit) return;
    this.emit('click', payload);
  }

  draw = () => {
    const {
      containerId, data, height,
      width, selected, transition,
      sizeRange, colorMatch, shuffle,
      fontRendering
    } = this.data
    const closeIconPath = 'M -50,40 L-40,50 0,10 40,50 50,40 10,0 50,-40 40,-50 0,-10 -40,-50 -50,-40 -10,0 -50,40'

    if (!Array.isArray(data)) {
      // Check if it is possible to draw with the current dataset
      console.warn('(n7-bubble-chart) The data object is not in the expected format!')
      return;
    }

    let t = d3
      .transition()
      .duration(0)
    if (typeof transition == 'number') {
      t = d3
        .transition()
        .duration(transition)
        .ease(d3.easeCubicInOut);
    }

    const colorMap = d3.scaleOrdinal()
      .domain(colorMatch ? colorMatch.domain : ['persona', 'luogo', 'organizzazione', 'cosa notevole'])
      .range(colorMatch ? colorMatch.range : d3.schemeTableau10)

    let sizeScale = d3 // map entity count to bubble size
      .scaleLinear()
      .domain([0, d3.max(data, d => +d.count)])
      .range([3, d3.max(data, d => +d.count)]);

    const pack = data =>
      d3
        .pack()
        .size([width - 2, height - 2])
        .padding(1.5)(
          d3.hierarchy({ children: data }).sum(d => sizeScale(d.count))
        );

    const root = () => { // if shuffle is undefined or true, shuffle the data
      if (typeof shuffle == 'undefined' || shuffle) {
        let shuffData = data.slice() // do not modify the source data!
        return pack(d3.shuffle(shuffData))
      } else { // if shuffle is set to false, skip the data shuffle
        return pack(data)
      }
    }

    const svg = d3
      .select(`#${containerId}`)
      .attr("viewBox", [0, 0, width, height])
      .attr("font-family", "Verdana, Geneva, sans-serif")
      .attr("text-anchor", "middle");

    const leaf = svg.selectAll('g').data(root().leaves(), d => d.data.entity.id);
    leaf
      .transition(t) // update transition on <g>
      .attr('fill-opacity', 1)
      .attr('transform', d => `translate(${d.x + 1},${d.y + 1})`)
      .attr('font-size', d => {
        var size = d.r / 5.5;
        size *= 1;
        size += 1;
        return Math.round(size) + 'px';
      })

    leaf.selectAll('.close-icon').remove() // clear all existing close icons
    if (selected) {
      leaf // render necessary close icons
        .filter(d => selected.includes(d.data.entity.id))
        .append('path')
        .attr('class', 'close-icon')
        .attr('d', closeIconPath)
        .attr('fill', '#fff')
        .attr('transform', d => {
          if (d.r / 4 > 3) {
            return `scale(.08) translate(0, ${d.r * 10 - 80})`;
          }
          return `scale(.08)`;
        });
    }

    leaf
      .select('circle')
      .transition(t) // update transition on <circle>
      .attr("fill-opacity", 1)
      .attr('r', d => d.r);

    leaf
      .select('text')
      .attr('font-family', d => {
        if (fontRendering && fontRendering.label && fontRendering.label.family) {
          return fontRendering.label.family
        } else {
          return 'inherit'
        }
      })
      .attr('font-weight', d => {
        if (fontRendering && fontRendering.label && fontRendering.label.weight) {
          return fontRendering.label.weight
        } else {
          return 'inherit'
        }
      })
      .selectAll("tspan")
      .data(d => {
        if (d.r / 4 > 4.5) {
          // show text and number threshhold
          let label = (
            d.data.entity.label.charAt(0).toUpperCase() +
            d.data.entity.label.slice(1)
          ).split(/ +/g);
          if (label.length > 3) {
            label = label.slice(0, 3);
            label[2] += 'â¦';
          }
          return label;
        } else if (d.r / 4 > 2.5) {
          // show text threshhold
          let label = (
            d.data.entity.label.charAt(0).toUpperCase() +
            d.data.entity.label.slice(1)
          ).split(/ +/g);
          if (label.length > 3) {
            label = label.slice(0, 3);
            label[2] += 'â¦';
          }
          return label;
        }
        return '';
      })
      .join('tspan')
      .attr("x", 0)
      .attr("y", (d, i, nodes) => `${i - (nodes.length + 1) / 2 + .97}em`)
      .attr('fill', 'white')
      .text(d => d)

    leaf
      .select('.label-count')
      .attr('font-family', d => {
        if (fontRendering && fontRendering.counter && fontRendering.counter.family) {
          return fontRendering.counter.family
        } else {
          return 'inherit'
        }
      })
      .attr('font-weight', d => {
        if (fontRendering && fontRendering.counter && fontRendering.counter.weight) {
          return fontRendering.counter.weight
        } else {
          return 'inherit'
        }
      })
      .attr('fill', 'white')
      .text(d => {
        if (d.r / 4 > 2.5) {
          // show text and number threshhold
          return d.data.count
        } else {
          return '';
        }
      })
      .attr('y', d => {
        let labelLength = d.data.entity.label.split(/ +/g)
        if (labelLength.length > 3) {
          labelLength = labelLength.slice(0, 3);
        }
        return `${labelLength.length - (labelLength.length + 1) / 2 + .97}em`
      })


    const g = leaf.enter().append('g');

    g.attr('transform', d => `translate(${d.x + 1},${d.y + 1})`)
      .attr('font-size', d => {
        var size = d.r / 5.5;
        size *= 1;
        size += 1;
        return Math.round(size) + 'px';
      })
      .attr('cursor', 'pointer')
      .on('click', (d, i) => {
        this.onClick(d.data.entity.id);
      })
      .attr("id", d => `g_${d.data.entity.id}`)
      .append("circle")
      .attr("id", d => (d.leafUid = d.data.entity.id))
      .attr('r', 0)
      .transition(t) // enter() transition on <circle>
      .attr("fill-opacity", 1)
      .attr('fill', d => colorMap(d.data.entity.typeOfEntity))
      .attr("r", d => d.r);

    g.append("clipPath")
      .attr("id", d => (d.clipUid = 'Clip-' + d.data.entity.id))
      .append("use")
      .attr("xlink:href", d => d.leafUid.href);

    g.append('text')
      .attr('font-family', d => {
        if (fontRendering && fontRendering.label && fontRendering.label.family) {
          return fontRendering.label.family
        } else {
          return 'inherit'
        }
      })
      .attr('font-weight', d => {
        if (fontRendering && fontRendering && fontRendering.label && fontRendering.label.weight) {
          return fontRendering.label.weight
        } else {
          return 'inherit'
        }
      })
      .selectAll("tspan")
      .data(d => {
        if (d.r / 4 > 4.5) {
          // show text and number threshhold
          let label = (
            d.data.entity.label.charAt(0).toUpperCase() +
            d.data.entity.label.slice(1)
          ).split(/ +/g);
          if (label.length > 3) {
            label = label.slice(0, 3);
            label[2] += 'â¦';
          }
          return label;
        } else if (d.r / 4 > 2.5) {
          // show text threshhold
          let label = (
            d.data.entity.label.charAt(0).toUpperCase() +
            d.data.entity.label.slice(1)
          ).split(/ +/g);
          if (label.length > 3) {
            label = label.slice(0, 3);
            label[2] += 'â¦';
          }
          return label;
        }
        return '';
      })
      .join('tspan')
      .attr("x", 0)
      .attr("y", (d, i, nodes) => `${i - (nodes.length + 1) / 2 + .97}em`)
      .attr('fill', 'white')
      .text(d => d)
      .attr('fill-opacity', 0)
      .transition(t) // enter() transition on <tspan>
      .attr('fill-opacity', 1);

    g.append('text') // Count label
      .attr('class', 'label-count')
      .attr('font-family', d => {
        if (fontRendering && fontRendering.counter && fontRendering.counter.family) {
          return fontRendering.counter.family
        } else {
          return 'inherit'
        }
      })
      .attr('font-weight', d => {
        if (fontRendering && fontRendering.counter && fontRendering.counter.weight) {
          return fontRendering.counter.weight
        } else {
          return 'inherit'
        }
      })
      .attr('fill', 'white')
      .text(d => {
        if (d.r / 4 > 2.5) {
          // show text and number threshhold
          return d.data.count
        } else {
          return '';
        }
      })
      .attr('y', d => {
        let labelLength = d.data.entity.label.split(/ +/g)
        if (labelLength.length > 3) {
          labelLength = labelLength.slice(0, 3);
        }
        return `${labelLength.length - (labelLength.length + 1) / 2 + .97}em`
      })
      .attr('fill-opacity', 0)
      .transition(t) // enter() transition on <text>
      .attr('fill-opacity', 1);

    leaf
      .exit() // EXIT CYCLE
      .remove();

    if (selected) {
      g.filter(d => selected.includes(d.leafUid)) // append 'X' icon // only for selected bubbles
        .append('path')
        .attr('d', closeIconPath)
        .attr('class', 'close-icon')
        .attr('fill', d => {
          if (d.r / 4 > 2.5) {
            return '#fff';
          }
          return 'transparent';
        })
        .attr('transform', d => {
          if (d.r / 4 > 3) {
            return `scale(.08) translate(0, ${d.r * 10 - 80})`;
          }
          return `scale(.08)`;
        });
    }

    this.emit('d3end', data) // communicate end of draw
  }
}
