// INDEX.ts
//---------------------------

import { Component, Input } from '@angular/core';

/**
 * Interface for an index component Item
 *
 * @property icon (optional)
 * @property text (required)
 * @property subtitle (required)
 * - text (required)
 * - icon (optional)
 * - grow (required)
 */

export interface IDataWidgetData {
  /**
   * item icon
   */
  icon?: string;
  /**
   * item text
   */
  text?: string;
  subtitle?: {
    /**
     * item subtitle
     */
    text: string;
    /**
     * item subtitle icon
     */
    icon: string;
    /**
     * percent rate
     */
    value: number | string;
    payload: any;
  }
  classes?: string;
  /**
   * mouse event payload
   */
  payload: any;
  _meta?: any;
}

@Component({
  selector: 'n7-data-widget',
  templateUrl: './data-widget.html'
})

export class DataWidgetComponent {
  @Input() data: IDataWidgetData;
  @Input() emit: any;

  onMouseEnter(payload) {
    if(!this.emit) return;
    this.emit('mouseenter', payload);
  }

  onMouseLeave(payload) {
    if(!this.emit) return;
    this.emit('mouseleave', payload);
  }

}
